import axios from "axios";



var BASE_URL = '';

const API_PATH = "/api";


//central api call of react app
function centralisedAPICall(url, method, data=null, headers=null) {
  method = method.toUpperCase();
  const Apiurl =  BASE_URL + API_PATH + url
  return axios({
    method,
    url: Apiurl,
    withcredentials: true,
    headers,
    data
  });
}

//api to fetch covid test data
export const CovidTestPackages = () => {
  return centralisedAPICall("/products", "get");
}

//api to logging in user
export const Loginapi = (data,headers) =>{
  return centralisedAPICall("/login",'post',data,headers)
}
//api to fetch logged in user cart
export const myCart = () => {
  return centralisedAPICall("/mycart", "get");
}
//add products to cart
export const addtoCart = (data) => {
  return centralisedAPICall(`/addtocart?${data}`, "get",data);
}
//remove products from cart
export const removeCartItem = (data) => {
  return centralisedAPICall(`/removeCartItem?${data}`, "get",data);
}
